import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'status'
})
export class StatusPipe implements PipeTransform {
  statusHash: any = {
    confirmed: ' xc nhn',
    new: 'Mi',
    processing: 'ang x l',
    delivering: 'ang giao hng',
    unpaid: 'Cha thnh ton',
    paid: ' thanh ton',
    successful: 'Thnh cng',
    completed: 'Hon tt',
    canceled: 'Hy'
  };

  transform(value: string): string {
    return this.statusHash[value] || 'Khng xc nh';
  }
}

@Pipe({
  name: 'statusvn'
})
export class StatusVN implements PipeTransform {
  statusHash: any = {
    pending: 'ang ch',
    published: ' ng b',
    new: 'Mi',
    stopped: 'Ngng bn',
    'error': 'ng b tht bi'
  };

  transform(value: string): string {
    return this.statusHash[value] || '';
  }
}
@Pipe({
  name: 'statusorder'
})
export class StatusOrder implements PipeTransform {
  statusHash: any = {
    P: ' xc Nhn',
    N: 'T Chi',
    Z: 'Mi',
  };

  transform(value: string): string {
    return this.statusHash[value] || value;
  }
}

@Pipe({
  name: 'vnd'
})
export class VND implements PipeTransform {
  transform(value: any): string {
    if (typeof value != "number" && typeof value != "string") {
      return value;
    }
    if (typeof value == "number") {
      if (value < 0) {
        return '-' + (-value).toFixed()
          .replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
      }
      return value.toFixed()
        .replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
    }
    if (typeof value == "string") {
      return value.replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
    }
    return value;
  }
}



