import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { RouterModule, Routes } from '@angular/router';
import { SharedServiceModule } from '../shared-services/shared-services.module';
import { FormsModule } from '@angular/forms';
import { IframeService } from '../shared-services/iframe.service';
import { MobileRoutes } from './mobile.route';

import 'rxjs/add/operator/takeUntil';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/distinctUntilKeyChanged';
import { HttpClientModule } from '@angular/common/http';
import { SharedPipeModule } from '../shared-pipes/shared-pipes.module';
import { MobileComponent } from './mobile.component';
import { LoginComponent } from '../pages/login/login.component';

const routes: Routes = MobileRoutes;

const components = [
  MobileComponent,
  LoginComponent,
];

const services = [IframeService];

@NgModule({
  declarations: [...components],
  imports: [
    CommonModule,
    HttpClientModule,
    SharedServiceModule,
    SharedPipeModule,
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(routes)
  ],
  providers: [...services],
  bootstrap: [MobileComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class MobileModule {}



