import {Component, OnInit, ViewChild} from '@angular/core';
import {UserService} from "../../shared-services/user.service";
import {Router} from "@angular/router";
import {AuthenticateService} from '../../shared-services/authenticate.service';
import * as validatecontraints from '../../../libs/validation-contraints';
import {ActivatedRoute} from "@angular/router";
import {AfterViewInit} from "@angular/core/src/metadata/lifecycle_hooks";
import {TelegramService} from "../../shared-services/telegram.service";
import { environment } from '../../../environments/environment';
import { GoogleAnalyticsService } from '../../shared-services/google-analytics.service';

declare const toastr: any;

@Component({
  selector: 'admin-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, AfterViewInit {
  @ViewChild("shopInfoModal") shopInfoModal;
  @ViewChild("onBoarding") onBoarding;
  app = 'dashboard';
  full_name;
  password;

  error = false;
  errorMessage = "";

  email;
  confirm;

  signupData: any = {};

  loading = false;

  provinces;
  resetPasswordLoading = false;
  formForgot: any = {};

  constructor(private userService: UserService,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private auth: AuthenticateService,
              private gaService: GoogleAnalyticsService,
              private telegramService: TelegramService) {
  }

  ngOnInit() {
    this.app = environment.app;
    this.userService.checkToken(this.auth.token).then((res: any) => {
      if (!res.error) {
        let ref = this.auth.getRef(true);
        let defaultRef = environment.app == "psx" ? "create-order" : "dashboard";
        this.router.navigateByUrl(ref || defaultRef);
      }
    }).catch(err => {
      this.auth.clear();
      this.router.navigateByUrl("/login");
    });
    // this.provinces = this.addressService.getLocations();
    // this.addressService.onLocationUpdated.subscribe(locations => this.provinces = locations);
  }

  ngAfterViewInit() {
    let action = this.activatedRoute.snapshot.queryParams['action'];

    // if (action && action == 'register') {
    //   $('#modal-register').modal('show');
    // }
  }

  async login() {
    this.loading = true;
    try {
      let res = await this.userService.login({
        login: this.email,
        password: this.password
      });
      this.gaService.login(this.email);
      this.auth.updateInfo({token: res.access_token, account: res.account});
      this.auth.updateUser(res.user);

      if (!res.account) {
        this.loading = false;
        // return this.onBoarding.show(new ShopInfoModel());
        // return this.shopInfoModal.show(new ShopInfoModel());
      }

      await this.setupAndRedirect();
    } catch (e) {
      debug.error("login", e);
      toastr.error(e.message, "ng nhp tht bi!");
    }
    this.loading = false;
  }

  async setupAndRedirect() {
    try {
      let sessionInfo = await this.userService.checkToken();
      // fetch token for multi-account
      let accounts = sessionInfo.available_accounts.filter(e => e.type == "shop");
      accounts.sort((a, b) => a.id > b.id);
      for (let account of accounts) {
        let accountRes = await this.userService.switchAccount(account.id);
        account.token = accountRes.access_token;
        account.shop = accountRes.shop;
      }
      this.auth.updateAccounts(accounts);
      this.auth.updateShop(sessionInfo.shop, true);
      this.auth.selectAccount(0);
      let ordersCount = 0;
      let ref = this.auth.getRef(true);
      this.router.navigateByUrl(ref || (ordersCount >= 0 ? "dashboard" : 'guide'));

    } catch (e) {
      debug.error("setupAndRedirect", e);
      toastr.error(e.message, "ng nhp tht bi!");
    }
  }

  async createAccount(shop) {
    try {
      shop.auto_create_ffm = true;
      let res = await this.userService.registerShop(shop);
      let id = res.shop.id;
      let accountRes = await this.userService.switchAccount(id);
      this.auth.updateInfo({token: accountRes.access_token, account: accountRes.account});
      this.auth.updateShop(accountRes.shop, true);
      // await this.setupAndRedirect();
      toastr.success("To ca hng thnh cng.");
    } catch (e) {
      this.auth.clear();
      toastr.error(e.message, "To ca hng tht bi");
    }
  }

  async doSignUp() {
    let signupData = this.signupData;
    this.error = false;
    try {
      if (!signupData.full_name) {
        throw new Error("Vui lng nhp Tn y .");
      }
      if (!signupData.phone || !signupData.phone.match(/^[0-9]{9,10}$/)) {
        throw new Error("S in thoi khng ng");
      }
      if (!validatecontraints.EmailValidates[0].check(signupData.email)) {
        throw new Error("a ch email khng hp l.");
      }
      if (signupData.password != signupData.confirm) {
        throw new Error("Mt khu nhp li khng chnh xc.");
      }
    } catch (e) {
      this.error = true;
      this.errorMessage = e.message;
      return;
    }
    try {
      let accSignUpData = {
        full_name: signupData.full_name,
        phone: signupData.phone,
        email: signupData.email,
        password: signupData.password
      }
      await this.userService.signUp(accSignUpData);
      this.telegramService.newMerchantMessage(accSignUpData);
      toastr.success("ng k thnh cng!");
      $('#modal-register').modal("hide");
    } catch (e) {
      toastr.error(e.message, "ng k khng thnh cng!");
    }
  }

  async doForgotPassword() {
    this.resetPasswordLoading = true;
    try {
      let email = this.formForgot.email;
      email = email && email.split(/-[0-9a-zA-Z]+-test/)[0] || '';
      email = email && email.split('-test')[0] || '';
      if (!validatecontraints.EmailValidates[0].check(email)) {
        throw new Error('a ch email khng hp l.');
      }

      await this.userService.requestResetPassword(this.formForgot.email);
      toastr.success("Mt email vi cc ch dn t li mt khu  c gi ti a ch email ca bn nu n tn ti trn h thng ca eTop.");
      (<any>$("#modal-forgot-password")).modal("hide");
    } catch (e) {
      toastr.error(e.message, "Yu cu mt khu mi tht bi!");
    }

    this.resetPasswordLoading = false;
  }

  validateEmail(formData) {
    if (!validatecontraints.EmailValidates[0].check(formData.email)) {
      this.error = true;
      this.errorMessage = "Email " + validatecontraints.EmailValidates[0].error;
    } else {
      this.error = false;
    }
  }

  clickBanner() {
    window.open("https://huongdan.etop.vn/giao-hang-noi-thanh-5000d-tu-8-8-den-15-8-2018", "_blank")
  }
}



